import React from 'react';
import Particles from 'react-particles-js';
import desktopSettings from './desktopSettings.json';
import mobileSettings from './mobileSettings.json';

export default class ParticleWave extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      vpWidth: null
    };

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ vpWidth: window.innerWidth });
  }

  componentWillUnmount() {
    this.state.vpWidth;
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  render() {
    const mobileStyles = {
      position: 'absolute',
      left: '0',
      top: '100px',
      animation: 'none'
    };

    const desktopStyles = {
      position: 'absolute',
      left: '0',
      top: '-130px',
      willChange: 'transform',
      animation: 'wave 10s infinite alternate'
    };

    let styles = desktopStyles;
    let settings = desktopSettings;

    if (this.state.vpWidth < 1024) {
      styles = mobileStyles;
      settings = mobileSettings;
    }

    return (
      <Particles
        params={this.state.vpWidth > 1024 ? dekstopSettings : mobileSettings}
        style={this.state.vpWidth > 1024 ? dekstopStyles : mobileStyles}
      />
    );
  }
}
